.applied-job-description-container {

  .row{
      margin-left:0px !important;
  }

  .jd-main-div{
    box-shadow:0 0 4px gray; 
    padding:15px;
    margin-top:20px;
    margin-bottom:10px;
    margin-left: -5px;
  }
  .jd-main-div-left{
    box-shadow:0 0 4px gray; 
    padding:15px;
    margin-top:20px;
    margin-bottom:10px;
    margin-left:5px;
  }
  .already-applied-job{
    color: green;
    border: 1px solid green;
    padding: 15px;
    border-radius: 5px;
    background-color: #e6ffe6;
  }

   /***** Accordion ******/
  .panel-heading {
    padding: 0;
    border:0;
  }
  .panel-title.jd-title> a, .panel-title.jd-title> a:active{
    display:block;
    padding: 2px 20px;
    color:  #ffffff;
    font-size:15px;
    text-transform:uppercase;
    text-decoration:none;
    background-color: #0080ff;
    font-family:'Malgun Gothic';
    margin: 0px !important;
  }
  .panel-heading  a:before {
     //font-family: 'Glyphicons Halflings';
     //content: "\e114";
     float: right;
     transition: all 0.5s;
  }
  .panel-heading.active a:before {
    display: inline-block;
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -o-transform: rotate(180deg); 
    transform: rotate(180deg);
  }
  .panel-body{
    border-top:transparent !important;
    margin-top: -15px;
    padding-top:0px;
    padding-bottom: 5px;    
  } 

overflow-x:hidden;

  /***** header css start ******/

  .main-header-app{
    width: 100%;
    float: left;
    position: relative;
    background: url(#{$imgPath}/hdr-bg.jpg) no-repeat center top;
    background-size: cover;
    padding: 8px 0 0 0;
  }
  .header-top {
    width: 100%;
    float: left;
    text-align: center;
  }
  .logo {
    width: 100%;
    float: left;
  }
  .logo img {
    margin: 0 auto;
  }
  .header-top .header-right-sec {
    width: 100%;
    float: left;
  }
  .header-top .input-group {
    margin: 15px auto;
    width: 100%;
    -webkit-transition:all 0.3s ease-in-out;
    -moz-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
  }
  .header-top .input-group .form-search{
    width: 100%;
    max-width: 245px;
    padding-right:66px;
    display: inline-block;
    vertical-align: middle;
    position: relative;
    -webkit-transition:all 0.3s ease-in-out;
    -moz-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
  }
  .header-top .input-group .input--filled{
    width: 100%;
    max-width: 300px;
    display: inline-block;
    vertical-align: middle;
    -webkit-transition:all 0.3s ease-in-out;
    -moz-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
  }
  .header-top .input--filled input{
    padding-left: 75px;
    -webkit-transition:all 0.3s ease-in-out;
    -moz-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
  }
  .header-top .input-group label{
    font-size: 12px;
    color: #FFF;
    pointer-events: none;
    position: absolute;
    top: 12px;
    left:12px;
    font-weight: normal;
    margin: 0;
    line-height: 1em;
  }
  .header-top input {
    width: 100%;
    border: #FFF 1px solid;
    height: 34px;
    background: transparent;
    font-size: 12px;
    color: #e8e8e8;
    padding: 0 10px;
    display:block;
    vertical-align: middle;
    outline: none;
  }
  .header-top button[type="submit"] {
    background: #fc9402;
    border: none;
    text-transform: uppercase;
    color: #FFF;
    font-family: 'MarkSimonsonProximaNovaSemibold';
    padding: 8px 19px;
    font-size: 13px;
    vertical-align: middle;
    display: inline-block;
    margin: 0px 0 0 0;
    box-shadow: rgba(0, 0, 0, 0.3) 1px 1px 2px;
    outline: none;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
    position: absolute;
    top: 0;
    right: 0;
  }
  .header-top button[type="submit"]:hover {
    background: #e08403;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .header-top .btn-default {
    max-width: 138px;
    display: block;
    height: 34px;
    font-family: 'MarkSimonsonProximaNovaSemibold';
    font-size: 13px;
    color: #FFF;
    font-weight: 600;
    width: 100%;
    padding: 0;
    margin: 10px auto;
    border-radius: 0;
    border: none;
    line-height: 34px;
    text-transform: uppercase;
    background: #00bef5;
    box-shadow: rgba(0, 0, 0, 0.3) 1px 1px 2px;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .header-top .btn-default.signin {
    background: #7ac043;
  }
  .header-top .btn-default:hover {
    background: #01a3d2;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .header-top .btn-default.signin:hover {
    background: #58ab16;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .header-top .toggle-menu{
    display: inline-block;
    vertical-align: middle;
    position: relative;
  }
  .header-top .toggle-menu .active-icon{
    display: none;
  }
  .header-top .menu-active .active-icon{
    display: block;
  }
  .header-top .menu-active .inactive-icon{
    display: none;
  }
  .header-top .toggle-icon {
    margin-left: 15px;
    display: inline-block;
    vertical-align: middle;
    margin-top: 12px;
    position: relative;
  }
  .header-top .toggle-icon img{
    z-index: 9;
    position: relative;
  }
  .header-top .toggle-icon:after {
    content: '';
    width: 50px;
    height:50px;
    position: absolute;
    top: -10px;
    left: -11px;
    right: 0;
    margin: 0 auto;
    background: #ebebeb;
    opacity: 0;
    -webkit-transition:all 0.3s ease-in-out;
    -moz-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
  }
  .header-top .nav-area{
    width: 130px;
    text-align: left;
    position: absolute;
    top: 40px;
    right:-12px;
    z-index: 999;
    //opacity: 0;
    pointer-events: none;
    transform: translateY(10px);
    -webkit-transition:all 0.3s ease-in-out;
    -moz-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
  }
  .header-top .menu-active .nav-area{
    opacity:1;
    pointer-events:auto;
    transform: translateY(0);
    -webkit-transition:all 0.3s ease-in-out;
    -moz-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
  }
  .header-top .menu-active .toggle-icon:after{
    opacity: 1;
    -webkit-transition:all 0.3s ease-in-out;
    -moz-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
  }

  .header-top .nav-area li a{
    display: block;
    line-height: 1em;
    padding: 12px;
    font-size: 15px;
    color: #41464e;
    background: #ebebeb;
  }
  .header-top .nav-area li a:hover{
    background: #0175c0;
    color: #FFF;
  }

  .body {
    width: 100%;
    float: left;
  }
  .body .panel-title {
    font-size: 24px;
    color: #434b58;
    font-family: 'MarkSimonsonProximaNovaSemibold';
    line-height: 1em;
    margin: 0 0 20px 0;
  }

  .main-footer {
    width: 100%;
    float: left;
    text-align: center;
    border-top: #5597ba 6px solid;
  }
  .main-footer .footer-upper-sec {
    width: 100%;
    float: left;
    background: #2b5565;
    padding: 30px 0;
  }
  .main-footer .grid-4,
  .main-footer .grid-5,
  .main-footer .grid-3 {
    width: 100%;
    float: left;
    padding: 0 15px;
  }
  .main-footer p.title {
    font-size: 22px;
    color: #e1e1e1;
    line-height: 1em;
    font-family: 'MarkSimonsonProximaNovaSemibold';
    margin: 0 0 10px 0;
  }
  .main-footer .footer-nav li {
    width: 100%;
    float: left;
    margin-bottom: 5px;
  }
  .main-footer .footer-nav li a {
    font-size: 14px;
    color: #FFF;
    font-weight: 300;
    display: block;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .main-footer .footer-nav li a:hover {
    color: #98b1bc;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .main-footer p {
    font-size: 14px;
    color: #98b1bc;
    font-weight: 300;
    line-height: 1.6em;
  }
  .main-footer .footer-logo {
    width: 100%;
    float: left;
    margin: 30px 0;
  }
  .main-footer .footer-logo img {
    margin: 0 auto;
  }
  .main-footer .demand-listing {
    overflow: hidden;
    margin-bottom: 15px;
  }
  .main-footer .demand-listing li {
    text-align: left;
    font-size: 14px;
    color: white;
    font-weight: 300;
    list-style-image: url(#{$imgPath}/blt.png);
    position: relative;
    left: 30px;
  }
  .main-footer .social-icons {
    width: 100%;
    float: left;
    margin-top: 14px;
  }
  .main-footer .social-icons a {
    display: inline-block;
    vertical-align: middle;
    margin: 0 6px;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .main-footer .social-icons a:hover {
    opacity: 0.6;
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  .copyright-bar {
    width: 100%;
    float: left;
    background: #19404f;
    padding: 28px 0;
  }
  .copyright-bar p {
    font-size: 15px;
    color: #788085;
    line-height: 1em;
    margin: 0;
  }
  /*******/

  @media only screen and (min-width: 370px) {
    .header-top .toggle-icon {
      margin-top: 6px;
