.content-wrapper.conference-meeting{

  .download-link {
    margin-right: 10px;
    margin-top: -8px;
  }
  .conference-header-div {
    min-width: 100%;
    height: 170px;
    background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
    color: $white;
    padding-top: 10px;
    padding-left: 10px;
    margin-top: 20px;

    .conference-header {
      //margin-top: -20px;
    }

    
    .link-button {
      border-radius: 6px;
      padding: 8px;
    }
    @media only screen and (max-width: 1266px) {
      .link-button {
        margin-right: 20px;
      }

    }

    }
  .main-panel {
    z-index: 1;
    background: $white;
    margin: -106px 10px 0px 10px;
    box-shadow: 6px -2px 9px grey;
    .main-row{
      margin-top: -15px;
      .first-row{
        border-right: 2px solid $lightestGrey;
        padding: 10px;
      }
    }
  }

  .form-in-meeting{
    margin:20px 0px 0px 0px;
  }

  .meet-header-1 {
    background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
    padding: 10px;
    margin-top:20px;
    width: 100%;
    text-align: center;
    color:$white;
  }
  .group-meeting{
    color: $black;
    border: 1px solid $lightestGrey;
    font-size: 30px;
    padding: 10px 12px 10px 11px;
    background: $lightestGrey;
    margin-left: -10px;
  }
  .video-icon{
    margin-left: 4px;
    font-size: $font-size-xlarge;
    color:#4289c8;
    vertical-align: middle;
  }
  .file-name {
    font-size: $font-size-xlarge;
    color:  #65aeee ;
  }
  .text-in-list{
    margin-left:8px;
  }
  .icon-div {
    border: 1px solid lightgrey;
    background: lightgrey;
  }
  .user-icon {
    font-size: 40px;
    margin-left: -10px;

  }
  .meet-video {
    width: 100%;
    height:450px;
    margin-top: 20px;
    background:#ccc;
    border-radius:0px;
    box-shadow: 5px 5px 5px #b3b3b3;
    margin-bottom: 10px;
  }
  .nav-pills > li > a:hover{
    border-radius: 21px 21px 0px 0px;
  }
  .nav-pills > li {
    float: left;
    background: lightgrey;
    border-radius:21px 21px 0px 0px;
    width: 171px;
    &:hover{
      border-radius:21px 21px 0px 0px;
    }
    &.active > a {
      color: #fff;
      background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
      border-radius:21px 21px 0px 0px;
      &:hover, &:focus {
        color: #fff;
        background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
        border-radius:21px 21px 0px 0px;
      }
    }
  }
  .laptop-icon-conference, .calender-icon-conference, .plus-user-icon-conference {
    font-size: 16px;
    .top-tab {
      margin-left: 8px;
      font-size: 16px;
      font-family: sans-serif;
    }

  }
  .participants-list{
    margin-left: -22px;
    font-weight: 600;
  }
  .editor{
    border: 1px solid lightgrey;
    padding: 18px;
  }
  .area-for-text{
    height: 160px;
    margin-top: -18px;
  }

  .table-header{
    background:$white;
  }

  /*.meeting-search{
    border-radius: 11px;

  }*/
  .meetactive {
    background:#7bb5e8 !important;
    color:#fff;
  }
  .up-down-icon{
    margin-right: 5px;

  }
  .list-of-meeting > li > .panel:hover {
    background:#7bb5e8;
    color:#fff;
    cursor:pointer;
    transition: .3s ease-in-out;
  }
  @media only screen and (max-width: 507px) {
    .nav-pills > li {
      float: left;
      width: 138px;
    }
  }
  @media only screen and (max-width: 458px) {
    .nav-pills > li {
      width: 62px;
      float: left;
    }
  }
  @media only screen and (max-width: 600px) {
    .meet-video {
      width: 100%;
      height:175px;
    }
  }
  input[type=date] {
    text-transform:uppercase;
  }

}
@media only screen and (max-width: 458px) {
  .pad-mobile{
    padding:0px !important;
  }
}


