.candidate-profile-signup{

   max-width:1332px;
    .text-of-agreement {
      margin-left: 8px;
    }
  @media only screen and (max-width: 992px) {
    .text-of-agreement {
      margin-top: -21px;
      margin-left: 24px;
    }
  }
  .selectdemoSelectHeader {
    .demo-header-searchbox {
      border: none;
      outline: none;
      height: 100%;
      width: 100%;
      padding: 0;
    }
    .demo-select-header {
      box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1), 0 0 0 0 rgba(0, 0, 0, 0.14), 0 0 0 0 rgba(0, 0, 0, 0.12);
      padding-left: 10.667px;
      height: 48px;
      cursor: pointer;
      position: relative;
      display: flex;
      align-items: center;
      width: auto;
    }
    md-content._md {
      max-height: 240px;
    }
  }
  md-radio-button {
    display: inline-block;
    margin-left: 15px;
  }
  .captcha-field{
    transform:scale(0.78);
    -webkit-transform:scale(0.78);
    transform-origin:0 0;
    -webkit-transform-origin:0 0;
  }
  .terms-condition-link{
    margin-left: 6px;
  }
  .agree-ondemand{
    margin-left:6px;
    margin-right: 0px;
  }
  .red-star{
    color:red !important;  
  }
.login-link{
  color:white;
  margin-left:8px;
}
  .candidate-submit-btn{
    border-radius: 0px;
    padding: 10px;
    line-height: 10px;
    width: 100px;
  }

  .mandatory-filed{
    color: $red;
    font-size: 18px;
  }

  .already-user{
    float: right;
  }
  .candidate-heading{
      margin-top: 45px;
  }
  .mandatory-star-note{
      margin-left:-15px;
      font-size: 12px;
  }
  
  .note-mandatory{
      font-size: 12px;
      color:grey;
  }
  .panel-heading{
    color:$white !important;
    background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%) !important;
    //background-image: none !important;
    border-radius: 0px;

    .panel-title{
      color: $white;
    }
  }
  
 // a{
    //  color:$green;
    //  margin-left: 8px;
  //}
  
 // a:hover{
  //    color:$green;
 // }
  
   .error-msg{
    font-size:11px;
    color: $red;
  }
  
  .error-lbl{
    color:$red;
    font-size:11px;
    font-weight:normal;
    word-spacing:1px;
    margin-left:20px;
  }

  .ui-select-container .ui-select-placeholder{
    line-height:0px;
  }

 .ui-select-bootstrap > .ui-select-match > .btn {
    text-align: left !important;
    border-radius: 0px;
  }
  .ui-select-match-text {
    line-height: 1.444;
  }
  .ui-select-search{
    width:100% !important;
  }
  .content-justify{
    text-align:justify;
  }
  .modal-body{
    font-size:12px;
  }
}
.terms-conditions-modal{
  .modal-header{
    color:$white;
    padding: 8px;
  }
  .modal-header .close {
    margin-top: 5px;
  }
}



