body,html{
  height: 100%;
}
section{
  padding:0px !important;
}

.navigation-container{
.nav-side-menu {
  overflow: hidden;
  font-size: 12px;
  font-weight: 200;
  position: fixed;
  top: 55px;
  width: 16%;
  height: calc(100% - 30px);
  height: -webkit-calc(100% - 30px);
  color: #181c20;
  border-left: 5px solid $sideNavColor;
  background: $white;
  z-index: 8;
  height: 100%;
   .brand {
        line-height: 50px;
        display: block;
        text-align: left;
        font-size: 20px;
        font-weight: 500;
        padding-left: 10px;

    }
    .toggle-btn {
        display: none;
    }
}
.sidebar-icon{
  font-size:16px;
  color:$sideNavColor;
  margin-left: -11px; 
}
.text-of-sidebar{
  margin-left:5px;
  font-size: 13px;
 font-weight: 600;
}

.nav-side-menu ul,
.nav-side-menu li {
  list-style: none;
  padding:0px;
  margin: 0px;
  line-height: 40px;
  cursor: pointer;
}


.nav-side-menu ul .sub-menu li.active,
.nav-side-menu li .sub-menu li.active,
.nav-side-menu ul .sub-menu li.active a,
.nav-side-menu li .sub-menu li.active a {
  color:black;
  background-color: white;
  font-weight: 700;
  font-size: 13px;
}

.nav-side-menu ul .sub-menu li,
.nav-side-menu li .sub-menu li {
  border: none;
  line-height: 28px;
  margin-left: 0px;
}

.nav-side-menu ul .sub-menu li:hover,
.nav-side-menu li .sub-menu li:hover {
  background-color: white;
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.nav-side-menu ul .sub-menu li:hover a,
.nav-side-menu li .sub-menu li:hover a {
  color: black;
  font-weight: 700;
  font-size: 13px;
}

.nav-side-menu li {
  padding-left: 10px;
  margin-top:10px;
    &:hover {
      color: #ff5e3a;
    }
    &:active {
      color: #ff5e3a;
    }
    a {
        text-decoration: none;
        padding-left: 10px;
        font-weight: 500;
        font-size: 12px;
        display: block;
        color:black;
        i {
            padding-left: 10px;
            width: 20px;
            padding-right: 20px;
          }
    }
}

@media (max-width: 767px) {
  .sidebar-box {
    width: 100%;
  }

  .nav-side-menu {
    position: relative;
    width: 100%;
    margin-bottom: 10px;
  }

  .nav-side-menu .toggle-btn {
    display: block;
    cursor: pointer;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 10 !important;
    padding: 3px;
    background-color: #fff;
    color: #000;
    width: 40px;
    text-align: center;
  }

  .brand {
    text-align: left !important;
    padding-left: 10px;
    line-height: 50px !important;
    font-size: 20px;
    font-weight: 500;
  }
}

@media (min-width: 767px) {
  .nav-side-menu .menu-list .menu-content {
    display: block;
  }
}
}


