.favorite-candidate-section {

  .favorite-candidate-heading{
    padding: 12px;
    line-height: 12px;
    background-color: #3f51b5;
    color: $white;
    font-weight: bold;
    border-bottom: 1px solid lightgrey;
  }
  .panel-body{
    min-height:260px !important;
  }
  .link-to-analytics{
    float: right;
    color: #015392 !important;
    cursor: pointer;
    
  }

  .panel-default > .panel-heading .link-to-analytics:after{
    content: "\f068";
    position: absolute;
    width: 36px;
    height: 100%;
    top: 0;
    right: 0;
  }
  .favorite-message {      
    text-align: center;
    margin-top: 65px;
    }
    @media only screen and (max-width: 480px){
      padding-left: 0px;
      padding-right: 0px;
    }
    
    .favorite-candidate {
      margin-left: 0px !important;
      margin-right: 0px !important;
    }
    
    .fav-candidate-icon{
      color: $white;
      margin-right: 5px;
    }
    
    .fav-cadidate-msg-icon{
      font-size: 5em;
      color: #009788;
      display: block;
    }
  .link-to-favourite{
    float: right;
    margin: -8px 0px;
  }
}





