.main-header {
    position:fixed;
    top:0px;
    z-index:9999;
    height:55px;
    width: 100%;
    float: left;
    padding: 10px 4px 13px;
    background: $headColor;
    font-family: $font-family;
    /* Old browsers */
    background: -moz-linear-gradient(top, $headColor 0%, $lightHeadColor 44%, $lightestHeadColor 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(top, $headColor 0%, $lightHeadColor 44%, $lightestHeadColor 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(to bottom, $headColor 0%, $lightHeadColor 44%, $lightestHeadColor 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    /*filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#009fe0', endColorstr='#015392', GradientType=0);*/
    /* IE6-9 */
    @media only screen and (max-width:500px){
      .margin-logo-candidate{
        // margin-left: 35px !important;
      }
      .menu-list-candidate{
        //margin-left: -195px !important;
        .navbar-collapse{
          width: 110px;
          ul{
            margin-top: 0px !important;
          }
        }
      }
       .desktop-logo{
            display: none;
        }
      .candidate-section{
        // margin-left:26px !important;
      }
      .navbar-default .navbar-toggle {
          background: none !important;
          border: none !important;
      }
      .navbar-default .navbar-toggle:hover, .navbar-default .navbar-toggle:focus{
          background-color: transparent !important;
          border: none !important;
      }
      .navbar-default .navbar-toggle .icon-bar {
          background-color: #f4f5f7;
      }
      .navbar-header{
        margin-top: -3px !important;
      } 
      .profile-section{
        padding-right: 0px !important;
        margin-left: 15px;
      }
      .profile-section-conference{
        padding-right: 0px !important;
        margin-left: -35px !important;
        .user-settings{
            margin-right: -20px !important;
            .settings-content{
              right: -35px !important;
            }
        }
      }
      .margin-logo{
        // margin-left: 20px !important;
      }
      .menu-list{
        // margin-left: -215px !important;
      }
      h3{
        margin: 5px !important;
        font-size:14px !important; 
      }
      .menu-section{
        width: 150px;
        ul{
            margin-top: 0px !important;
        }
      }
      .notification-box{ 
        .dropdown-menu{
            margin-top: 10px !important;
            min-width: 235px !important;
        }
      } 
      .company-logo-img{
          height: 40px !important;
          width: 130px !important;
          float: right;
      }
    }

  .logo-position{
        height: 49px !important; 
        width: 55px !important;
        border:none;
        outline:none; 
        margin-top:0px;
    }
    .navbar-default {
        background: transparent;
        border: none;
        margin-bottom: 0;
        box-shadow: none !important;
    }
    .navbar-default .navbar-toggle {
        background: $white;
    }
    .nav {
        width: inherit !important;
        //width: 100%;
        /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#81dcf8+0,65aeee+46,65aeee+46,4289c8+100 */
        background: $navColor;
        /* Old browsers */
        background: -moz-linear-gradient(top, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
        /* FF3.6-15 */
        background: -webkit-linear-gradient(top, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        /*filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#81dcf8', endColorstr='#4289c8', GradientType=0);*/
        /* IE6-9 */
        li {
            a {
                font-size: 16px;
                color: $white !important;
                padding: 9px 19px !important;
            }
            &.active a, a:hover {
                color: $lightestHeadColor  !important;
                background: $navWhite;
                /* Old browsers */
                background: -moz-linear-gradient(top, $navWhite 0%, $navLightWhite 48%, $navLightestWhite 100%);
                /* FF3.6-15 */
                background: -webkit-linear-gradient(top, $navWhite 0%, $navLightWhite 48%, $navLightestWhite 100%);
                /* Chrome10-25,Safari5.1-6 */
                background: linear-gradient(to bottom, $navWhite 0%, $navLightWhite 48%, $navLightestWhite 100%);
                /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                /*filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#fefefe', endColorstr='#ececec', GradientType=0);*/
                /* IE6-9 */
            }
        }
    }
    .navbar-default .navbar-nav > .active > a {
        color: $lightestHeadColor !important;
        background: $navWhite;
        /* Old browsers */
        background: -moz-linear-gradient(top, $navWhite 0%, $navLightWhite 48%, $navLightestWhite 100%);
        /* FF3.6-15 */
        background: -webkit-linear-gradient(top, $navWhite 0%, $navLightWhite 48%, $navLightestWhite 100%);
        /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, $navWhite 0%, $navLightWhite 48%, $navLightestWhite 100%);
        /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        /*filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#fefefe', endColorstr='#ececec', GradientType=0);*/
        /* IE6-9 */
        &:focus, &:hover {
            color: $lightestHeadColor !important;
            background: $navWhite;
            /* Old browsers */
            background: -moz-linear-gradient(top, $navWhite 0%, $navLightWhite 48%, $navLightestWhite 100%);
            /* FF3.6-15 */
            background: -webkit-linear-gradient(top, $navWhite 0%, $navLightWhite 48%, $navLightestWhite 100%);
            /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(to bottom, $navWhite 0%, $navLightWhite 48%, $navLightestWhite 100%);
            /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            /*filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#fefefe', endColorstr='#ececec', GradientType=0);*/
            /* IE6-9 */
        }
    }
    .logo {
        text-align: left;
        margin: -4px 0 15px 0;
        .company-logo-img{
            height:45px !important;
            width:140px !important;
        }
    }
    .ondemand-logo {
      text-align: left !important;
        margin: -4px 0 15px 0 !important;
    }

    .navbar {
        min-height: 45px;
        margin-top: -2px;
    }




    .user-login {
        width: 100%;
        float: left;
        text-align: center;
        .small-logo {
            //margin-top: 15px;
            display: block;
        }
    }

    .user-settings{
        display:block;
        float:right !important;
        cursor: pointer;
        .user-logo{
          img{
              height: 52px !important;
              margin-top: -9px;
              width: 52px !important;
              border: none;
              outline: none;
              float:right;
            //   margin-left: 28px;
          }
            .user-image{
                border-radius: 50%;
                background: $lightestGrey;
            }
        }    
        .settings-content{          
            border-radius: 0px;
            background: $lightestGrey;
            width: 200px;
            color: blue;
            right: 0px;
            left: inherit;
            font-size: $font-size-normal;
            margin-top:-4px;
            h5{
                color: $darkGrey;
                text-align: center;
                border-bottom: 1px solid $lightGrey;
                padding-bottom: 5px;
            }

            li{
                line-height: 30px;
                display: block;
                padding: 0px 25px 0px 10px;
                color: $blue;
                i{
                    padding-right: 10px;
                    margin-left:0px;
                }
                &:hover {
                    color: $blue;
                    font-weight: 700;
                    font-size: $font-size-normal + 1;
                    -webkit-transition: all 1s ease;
                    -moz-transition: all 1s ease;
                    -o-transition: all 1s ease;
                    -ms-transition: all 1s ease;
                    transition: all 1s ease;
                    cursor:pointer;

                }
            }        


            &:after, &:before {
                bottom: 100%;
                left: 83%;
                border: solid transparent;
                content: " ";
                height: 0;
                width: 0;
                position: absolute;
                pointer-events: none;
            }
            &:after, &:before {
                border-color: rgba(233, 234, 238, 0);
                border-bottom-color: #e9eaee;
                border-width: 7px;
                margin-left: -13px;
            }
        }
    }
    .notification-box{
        .dropdown-box{
            top: 45px;
            color: black;
            left: 2%;
        }
        .glyphicon{
            top: -11px; 
            left: 6px;
        }
        .view-box{
            padding: 10px;
            float: right;
            color: #009fe0;
        }
        .dropdown-menu{
            border-radius: 0px ;
            padding: 5px 10px !important;
            color: black;
            min-width: 335px;
            margin-top: 10px;
            position: absolute;
            right: -10px;
           }
        .dropdown-menu:before {
            position: absolute;
            top: -7px;
            right: 15px;
            display: inline-block;
            border-right: 7px solid transparent;
            border-bottom: 7px solid #ccc;
            border-left: 7px solid transparent;
            border-bottom-color: rgba(0, 0, 0, 0.2);
            content: '';
          }

        .dropdown-menu:after {
          position: absolute;
          top: -6px;
          right: 15px;
          display: inline-block;
          border-right: 6px solid transparent;
          border-bottom: 6px solid #ffffff;
          border-left: 6px solid transparent;
          content: '';
        }
        .badge-section{ 
          .badge{
            margin-top: 8px;
            font-weight: normal !important;
            h3{
                padding-left: 10px;
            }
            ul{
                max-width: 380px;
                border-radius: 0px;
                min-width: 260px;
                min-height: 110px;
            }
          }
        }

        @media only screen 
        and (max-device-width: 768px) 
        { 
            .notification-bell {
                 margin-left: 0em;
            } 
                
            }

        @media only screen 
        and (min-device-width: 769px) 
        {
            .notification-bell {
                    //margin-left: 14em;
                } 
                
            }

    }
    .help-block {
    text-decoration: none;
    padding: 0px;
    color: #337AB7;    
   } 
    .help-block:hover {      
      font-weight: bold;
      color: #337AB7;
      background: none;
   }
    .color-logout{
        color:$red !important;
    }
 }

/* ----------- iPhone 5 and 5S ----------- */

/* Portrait and Landscape */
@media only screen 
  and (min-device-width: 320px) 
  and (max-device-width: 568px)
  and (-webkit-min-device-pixel-ratio: 2) {
    .main-header {
        .navbar-default .navbar-toggle {
            margin-right:-20px;
        }
        .menu-section ul{
            margin-left:15px;
        }
        .profile-section{
            margin-left:0px;
        }
        .desktop-logo{
            display: none;
        }
       
    }
     
}
/* Portrait and Landscape */
@media only screen and (max-width:325px){

    .main-header {
        .navbar-default .navbar-toggle {
          margin: 5px 0px 0px;
          float: left;
          position: fixed;
        }
        .menu-section{
            width: 150px;
            margin-top: 50px;
            margin-left: 33px;
        }
        .profile-section{
            margin-left:0px;
        }
         .desktop-logo{
            display: none;
        }
    }
}

/* ----------- Galaxy S5 ----------- */

/* Portrait and Landscape */
@media screen 
  and (device-width: 360px) 
  and (device-height: 640px) 
  and (-webkit-device-pixel-ratio: 3) {
    .main-header {
        .navbar-default .navbar-toggle {
            margin-right:10px;
        }
        .menu-section{
            width:150px;
            ul{
            margin-left:-7px;
            }
        } 
         .desktop-logo{
            display: none;
        }
    }
}



/* ----------- iPhone 6 ----------- */

/* Portrait and Landscape */
@media only screen 
  and (min-device-width: 375px) 
  and (max-device-width: 667px) 
  and (-webkit-min-device-pixel-ratio: 2) { 
    .main-header {
        .navbar-default .navbar-toggle {
            float: left;
            margin: 5px 0px 0px;      
        }
        .menu-section{
            width: 120px;
            padding: 0px;           
            ul{
              margin: 0px;
              float: left;
              margin-left:0px;
            }
        } 
         .desktop-logo{
            display: none;
        }
        .user-settings{
          max-width: 85px;          
          padding: 0px;
        }
     }
}

/* ----------- iPhone 6+ ----------- */

/* Portrait and Landscape */
@media only screen 
  and (min-device-width: 414px) 
  and (max-device-width: 736px) 
  and (-webkit-min-device-pixel-ratio: 3) { 
    .main-header {
        .navbar-default .navbar-toggle {
            float: left;
            margin-left: -10px;
        }
        .menu-section{
            width:150px;
            margin-left: -28px;
            ul{
            margin-left:-7px;
            }
        } 
         .desktop-logo{
            display: none;
        }
    }
}

@media only screen 
  and (max-device-width: 768px) 
   { 
        .desktop-logo{
            display: none;
        }
        .notification-bell {
            margin-left: 0em;
        } 
        
    }


@media only screen 
  and (min-device-width: 769px) 
   { 
        .mobile-logo{
            display: none;
        }
        notification-bell {
            margin-left: 14em;
        } 
        
    }







