.dashboard-notification-section {
    @media only screen and(max-width:500px){
      .notification-message{
        margin-left: 0px !important;
      }
      .main-table-area{
        padding:0px !important;
        margin-top: 0px !important;
      }
      .showMe{
        display: none;
      }
/*      .notification-list{
        min-height: 500px !important;
      }*/
    }
    .view-notification-heading{
      padding:0px !important;
    }
    .notification-list{
      //max-height:450px;
      overflow-y:hidden;
      overflow-x:hidden;
    }
  
    .notification-heading{
      padding: 13px !important;
      line-height: 12px;
      background-color: #3f51b5;
      color: $white;
      font-weight: bold;
      border-bottom: 1px solid lightgrey;
  }
  .pagination{
      margin-bottom: 10px !important;
      margin-left: 10px !important;
  }
  .panel-body{
      .notification-list{
        min-height: 440px;
        max-height: 492px;     
      }
      .list-notify{
            min-height: 632px;
            max-height: 635px;
      }
      table{
        thead{
          display:none;
        }
      }
  }  
  .notification-message {
    margin-left: 17px;
    font-size: 12px;
    color: #015392;
    padding: 7px;
  }
  
  .notification-no-message{
    text-align: center;
    margin-bottom: 200px;
  }
  
  .notification-icon-msg{
    font-size: 5em;
    color: #009788;
    display: block;
  }
  
  .notification-icon{
    color: $white;
    margin-right: 5px;
  }
  
  .view-all-box{
    height: 23px;
    font-size: 16px;
    font-weight:600;
    margin: 15px;
    text-decoration: none;
    cursor: pointer;
  }
}



