.content-wrapper.conference-home {

  @mixin placeholder {
    &::-webkit-input-placeholder {@content}
    &:-moz-placeholder           {@content}
    &::-moz-placeholder          {@content}
    &:-ms-input-placeholder      {@content}  
  }
  .btn-dimension{
    min-width:80px;
  }
  .coloumn-set {
    width: 10%;
  }
  .end-heading-position{
    margin-top: 215px;
    text-align: center;
  }

  .import-contact-list{
    margin-top: 25px;
  }

  .download-link {
    margin-right: 10px;
    margin-top: -8px;
  }
  
    .cantact-list{
      margin-top: 10px;
  }
  
  .btn-position{
        margin-top: -4px;
    }

  .contact-list-info{
    margin-top: 30px;
  }
    
    .add-contact-btn{
        margin-top: 4px;
    }
    
    .evaluator-filter-dropdown{
        margin-left: -235px !important;
        width: 300px;
        border-radius: 0px;
        margin-top: 0px;
    }
    .contact-view-icon{
        background: transparent!important;
        border: none!important;
    }
  .conference-header-div {
    min-width: 100%;
    height: 170px;
    background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
    color: $white;
    padding-top: 10px;
    padding-left: 10px;
    margin-top: 20px;

    .conference-header {
     // margin-top: -20px;
    }
    .link-button {
      border-radius: 6px;
      padding: 8px;
    }
    @media only screen and (max-width: 1266px) {
      .link-button {
        margin-right: 20px;
      }

    }

  }
  
  .meeting-password-textbox {
    position: absolute;
    margin-left: -30px;
  }

  .search-btn {
    position: absolute;
    margin-top: -34px;
    margin-left: -1px;
    border-radius: 0px;
  }

  .schedule-conference {
    color: #4289c8;
    margin-top: 20px;
    visibility: hidden;
  }
  .scheduleConference {
    .meetNow-form-container{
      //margin-top: 11px;
      height: 100%;
      background: #f4f5f7;
      width: 100%;
     // margin-left: 10px;
      padding-bottom: 44px;
    }

  .date-picker-btn{
     margin-top:-34px;
     float:right;
     margin-right: 38px;

  }
    .form-control {
      border: 1px solid #4289c8;
      
    }
    .area-for-text{
      height:54px!important;
    }
    input[type=date] {
      text-transform:uppercase;
    }
  }
  .checkbox-label {
    font-weight: normal;
  }
  .main-panel {
    z-index: 1;
    background: $white;
    margin: -106px 10px 0px 10px;
    box-shadow: 6px -2px 9px grey;

    .laptop-icon-conference, .calender-icon-conference, .plus-user-icon-conference {
      font-size: 16px;
      .top-tab {
        margin-left: 8px;
        font-size: 16px;
        font-family: sans-serif;
      }

    }
    label {
      font-weight: 600;
    }

    .upcoming-meetings {
      color: #4289c8;
    }
    .view-all-link {
      margin-top: 8px;
      font-size: 16px;
    }
    .upcoming-meetings-heading {
      background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
      color: $white;
    }
    .user-icon {
      font-size: 40px;
      margin-left: -10px;

    }
    .icon-div {
      border: 1px solid lightgrey;
      background: lightgrey;
    }

    .btn-cancel {
      background: #ccc;
      border: none;
      color: white;
      &:hover {
        background: #aaa;
      }

    }
    .meeting-text {
      font-weight: bold;
    }
    .meeting-inform-row {
      border-bottom: 1px solid lightgrey;
      padding-bottom: 10px;
      padding-top: 10px;
    }
    .nav-pills > li {
      float: left;
      background: lightgrey;
      border-radius: 8px 8px 0px 0px;
      width: 171px;
      &.active > a {
        color: #fff;
        background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
        &:hover, &:focus {
          color: #fff;
        background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%);
        }
      }
    }

    @media only screen and(max-width:1070px){
      .nav-pills > li{
        width:auto;
      }
    }

    .meetNow {
      width: 100%;
      border: 1px solid $navColor;
      min-height: 100%;
      padding:10px 10px 10px 10px;

      .meetNowForm {
        width: 100%;
        height: 132px;
        background: #f4f5f7;
        //margin: 24px 13px 10px 18px;
        position: relative;
        padding-top: 20px;
        .meetNowField {
          padding-left: 22px;
        }
        .cancelMeeting {
          margin-top: 10px;
        }

        .start-cancel-btn {
          padding-right: 30px;
        }
        .meeting-number-btn {
          padding-right: 0px;
        }
        .btn-cancel {
          background: #ccc;
          border: none;
          color: white;
          &:hover {
            background: #aaa;
          }

        }
      }
    }
    //@media only screen and (max-width: 507px) {
      //.nav-pills > li {
        //float: left;
        //width: 138px;
      //}
    //}
    @media only screen and (max-width: 458px) {
      .nav-pills > li {
        width: 52px;
        float: left;
      }
      .evaluator-filter-dropdown{
        margin-left: -163px !important;
        width: 228px;
      }
    }

    @media only screen and (max-width: 766px) {

      .meetNow {
        width: 100%;
        border: 1px solid #4289c8;
        min-height: 260px;
        padding-bottom: 10px;

        .meetNowForm {
          width: 100%;
          height: 250px;
          background: #f4f5f7;
          //margin: 24px 13px 10px 5px;
          position: relative;
          padding-top: 20px;
          .meetNowField {
            padding-left: 22px;
          }
          .cancelMeeting {
            margin-top: 10px;
          }
          .start-cancel-btn {
            padding-right: 30px;
          }
          .meeting-number-btn {
            padding-right: 0px;
          }
          .btn-cancel {
            background: #ccc;
            border: none;
            color: white;
            &:hover {
              background: #aaa;
            }

          }
          .meeting-number-box {
            margin-top: 30px;
          }
        }
      }
    }
  }
  @media only screen and (max-width: 766px) {
    .btn-dimension{
      max-width:80px;
    }
    .btn-cancel{
      margin-top:10px;
      min-width:80px;
    }
    .main-panel {
      margin-top: -106px;
    }

    .start-btn,.btn-can{
      margin-top:10px;
      min-width:80px;
    }
    .btn-can{
      min-width:80px;
    }
    .minute-select-box{
      margin-top:10px;
    }
  }
  .start-btn{
    background:#7ac043;
    border:#7ac043;
    //opacity: 0.6;
    color:white;
    &:hover{
      background: #6bac39
    }
  }
  .error-msg {
    color: red;
    font-size: 10px;
  }
  .mandatory-field {
    color: red;
  }
  .uib-increment.hours{

    .btn-link{
      margin-top: -46px;
      outline:none;
    }
    .glyphicon-chevron-up{
      font-size:10px;
    }
  }
  .uib-increment.minutes{

    .btn-link{
      margin-top: -46px;
      outline:none;
    }
    .glyphicon-chevron-up{
      font-size:10px;
    }
  }
  .uib-time.hours{
    .form-control{
      margin-top:-37px;
      height:34px;
      width: 54px;
    }
  }
  .uib-time.minutes{
    .form-control{
      margin-top:-37px;
      height:34px;
      width: 54px;
    }
  }
  .uib-decrement.hours{

    .btn-link{
      margin-top: -42px;
      outline:none;
    }
    .glyphicon-chevron-down{
      font-size:10px;
    }
  }
  .uib-decrement.minutes{

    .btn-link{
      margin-top: -42px;
      outline:none;
    }
    .glyphicon-chevron-down{
      font-size:10px;
    }
  }
  .uib-time.am-pm{
       .btn.btn-default{
         margin-top:-43px;
         width: 44px;
      }
   }
  .time-picker-div{
    margin-top: 6px;
  }
  .uib-separator{
    visibility:hidden;
  }
  input::-webkit-inner-spin-button {
    display: none;
  }
  .form-control{
    border-radius:0px !important;
    padding-left: 10px;
  }
  .btn-cancel-meeting{
    margin-left: -37px;
  }

  input[type="search"] { @include placeholder {
    color: #999999;
  }}

  input[type=date] { @include placeholder {
    color: #999999;
  }}
.showMe {
    display: none;
 }
 
 .showColumn {
    display: block;
  }

@media only screen and (max-width: 480px) {
  .showMe {
    display: block;
  }
  .showColumn {
    display: none;
  }
}

    
}
.confprepheading{
  color:white;
  width:300px;
  text-align:center;
  background:#7bb5e8;
  padding: 9px;
  font-size: 18px;
}
.ui-select-multiple.ui-select-bootstrap.ui-select-container input.ui-select-search { width: 100% !important; }

@media only screen and (max-width: 458px) {
  .pad-mobile{
    padding:0px !important;
  }
}

@media only screen and (max-width: 766px) {
  .add-contact-btn{
    margin-top:20px !important;
  }
  .delete-btn-pos{
    margin-top:14px !important;
  }
}




