.home-landing {
  .welcome-head {
    margin-bottom: 50px;
    border-bottom: 2px solid $blue;
    color: $blue;
  }
}

.loginbox{
  margin-top:10px;
  border-radius: $global-radius;

/*  input[type="text"], input[type="password"]{
    height: 40px;
    margin-top: 20px;
  }*/
/*  input[type="checkbox"]{
    height: 25px;
    width: 25px;
    margin-left: -30px !important;
  }*/
  input[type="checkbox"]:checked + .checkbox::after {
    color: $maxDarkBlue;
  }
  .checkbox label {
    padding-left: 30px;
    line-height: 35px;
    font-size: 12px;
  }

  button {
    height: 40px;
    font-size: 14px;
  }

  .panel {
    border-radius: $global-radius;
  }
  .top-margin{
    margin-top:30px;
  }

  .error-msg{
    font-size:11px;
    color: red;
  }

  .panel-heading{
    color:white;
    background: linear-gradient(to bottom, $navColor 0%, $navDarkColor 46%, $navDarkColor 46%, $navDarkerColor 100%) !important;
    //background-image: none !important;
    border-radius: 0px;

    .panel-title{
      color: $white;
    }
  }

  .panel-link{
    float:right;
    font-size: 80%;
    position: relative;
    //top:-20px;
  }

  .panel-link a{
    color:white;
  }

  .bottom-link{
    text-align:center;
    font-size: 12px;
  }

  .btn-success{
    border-radius: $global-radius;
  }

  .error-lbl{
    color:red;
    font-size:12px;
    font-weight:normal;
    word-spacing:1px;
    margin-left:20px;
  }

  .user-type{
    margin-right: 20px;
    font-weight: bold;
  }

  .lbl-user-type{
    margin-left: 10px;
    font-weight: normal;
  }
  .ui-select-container .ui-select-placeholder{
    line-height:0px !important;
  }
  .error-message-email{
    color:$red;
    margin-left:14px;
    font-size:12px;
  }

  .panel-info {
    border-color: $navColor;
  }

  .left-float{
    float:left;

  }
}
@media only screen and(max-width:360px){
  .sign-in-header {
    font-size: 13px;
  }
  .panel-link a{
    font-size:11px;
  }
  .login-panel{
    padding:4px !important;
  }
}

.login-page-container{
  //background:$lightGrey !important;
  background: $mediumGrey; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient($mediumGrey, $white); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient($mediumGrey, $white); /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient($mediumGrey, $white); /* For Firefox 3.6 to 15 */
  background: linear-gradient($mediumGrey, $white); /* Standard syntax (must be last) */
  height:100%;
  overflow:hidden;
  .login-panel-tab{
    padding:10px;
    border:1px solid lightgrey;
    border-top:none;
    background:$white;
  }
  .signin-tabs{
    margin-top:10px;
    background:$white;
  }
  .signin-tabs > li {
    width:50% !important;
    margin-left:0px !important;
    border-radius:0px;
    font-weight:600;
    color:$darkGrey;
    a{
      border-radius:0px;
      color: $maxDarkBlue;
      margin-right:-1px;
      font-size: 14px;
      text-align: center;
      border-top: 5px solid transparent;
    }
  }
  .signin-tabs > li.active > a, .signin-tabs > li.active > a:hover, .signin-tabs > li.active > a:focus {
    color: $maxDarkBlue;
    cursor: default;
    border-top: 5px solid $maxDarkBlue;
    border-bottom-color: transparent;
    margin-right:-1px;
    border-radius:0px;
    font-size: 14px;
    text-align: center;

  }
  .image-div {
    background: url(#{$imgPath}/webrtcBg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    width:100%;
    height:600px;
    margin-top:-15px;
    margin-left:-30px;
  }
  .image-div-can {
    background: url(#{$imgPath}/webrtcBg5.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    width:100%;
    height:600px;
    margin-top:-15px;
    margin-left:-30px;
  }
  .image-container{
    position:relative;
    width:100%;
    max-height: 662px;
    min-height: 638px;
    height:inherit;

  }
  .input-group-addon{
    border-radius:0px;
    color:$darkGrey !important;
    background:$lightGrey;
  }
  .pipe-char{
    padding:3px;
  }
  .oda-image-header{
    padding:10px;
    margin-top: -78px;
    //background-color: $lightHeadColor;
    text-align:center;
  }
  .form-group{
    margin-bottom:0px;
  }
  .second-text-box{
    margin-top:16px;
  }
  .canimg,.text-div-can{
    opacity:0;
  }
  .canimg,.usrimg{
    position:absolute;
    //height:100%;
    -webkit-transition: all 2s; /* For Safari 3.1 to 6.0 */
    transition: all 2s;
    max-height: 662px;
    min-height: 638px;
  }
  .text-div-can,.text-div-user{
    position:absolute;
    color:white;
    font-size: 21px;
    left: 30px;
    top: 240px;
    font-weight: 600;
    -webkit-transition: all 2s; /* For Safari 3.1 to 6.0 */
    transition: all 2s;
    .featureList{
      display: block;
      list-style-type: disc;
      font-size: 18px;
      font-family: monospace;
      font-style: italic;
    }
  }
  .text-div-can{
    //display:none;
    position:absolute;
  }
  .second-section{
    padding:130px 20px 0px 0px;
  }
  .recaptca-dimension{
    transform:scale(0.77);
    -webkit-transform:scale(0.77);
    transform-origin:0 0;
    -webkit-transform-origin:0 0;
  }
  @media only screen and(max-width:467px){
    .text-div-can,.text-div-user{
      display:none;
    }
    .second-section{
      padding:130px 20px 0px 19px;
    }
    .oda-image-header{
      padding:10px;
      margin-top: -756px;
      //background-color: $lightHeadColor;
      text-align:center;
    }
    .signin-tabs > li{
      width:50% !important;
    }
    .signin-tabs > li a{
      font-size:13px;
    }
    .canimg,.usrimg{
      position:fixed;

    }
  }
  @media only screen and(min-width:470px){
    .second-section{
      padding: 130px 22px 52px 22px;
    }
  }
  .submit-button{
    margin:0px;
  }

}


