.invitation-tracking-section {
  @media only screen and(max-width:500px){
      .funnel-chart { 
        width: 250px !important;  
        .trapezoid_01 {
          width: 250px !important;
          margin-left: 0px !important;
        }
        .trapezoid_02 {
          width: 220px !important;
          margin-left: 15px !important;
        }

        .trapezoid_03 {
          width: 190px !important;
          margin-left: 30px !important;
        }

        .trapezoid_04 {
          width: 160px !important;
          margin-left: 45px !important;
        }
        .trapezoid_05 {
          width: 130px !important;
          margin-left: 60px !important;
        }

        .trapezoid_06 {
          width: 100px !important;
          margin-left: 75px !important;
        }
        .funel-border{
          border-left: 15px solid transparent !important;
          border-right: 15px solid transparent !important;          
        }
      }
    }
  .invitation-tracking-heading{
    padding: 12px !important;
    line-height: 12px;
    background-color: #3f51b5;
    color: $white;
    font-weight: bold;
    border-bottom: 1px solid lightgrey;
    }
  .panel-body{
    min-height:260px !important;
  }
/*  .funel-chartbox{
    margin-top: 35px;
    text-align: center;
  }*/
  .note-text{
    font-size:11px;
    margin-left: 5px;
  }
  
  .invitation-trac-icon{
    color: $white;
    margin-right: 5px;
  }
  
  .funnel-chart { 
    width: 280px;
    text-align: center;
    padding-bottom:10px;
    .funel-border{
      border-left: 20px solid transparent;
      border-right: 20px solid transparent;          
    }
    .trapezoid_01 {
      border-top: 35px solid #e2493d;
      height: 0px;
      width: 280px;
      margin-left: 0px;
        &:hover{
                      border-top: 35px solid #d81b0d;
                  }
    }
    .trapezoid_02 {
      border-top: 35px solid #795548;
      height: 0;
      width: 240px;
      margin-left: 20px;
      &:hover{
            border-top: 35px solid #d81b0d;
        }
    }
  
    .trapezoid_03 {
      border-top: 35px solid #FF7F0E;
      height: 0;
      width: 200px;
      margin-left: 40px;
      &:hover{
                      border-top: 35px solid #d81b0d;
                  }
    }

    .trapezoid_04 {
      border-top: 35px solid #00BCD4;
      height: 0;
      width: 160px;
      margin-left: 60px;
      &:hover{
              border-top: 35px solid #d81b0d;
          }
    }
    .trapezoid_05 {
      border-top: 35px solid #9E9E9E;
      height: 0;
      width: 120px;
      margin-left: 80px;
      &:hover{
            border-top: 35px solid #d81b0d;
        }
    }
  
    .trapezoid_06 {
      border-top: 35px solid #8BC34A;
      height: 0px;
      width: 80px;
      margin-left: 100px;
        &:hover{
              border-top: 35px solid #d81b0d;
          }
    }

    .text-box {
      font-size: 12px;
      color: white;
      margin-top: -25px;
      text-align: center;
    }
  }
}



